<template>
  <div class="my-navbar" style="direction: ltr">
    <b-navbar :class="{ 'onScroll': !view.topOfPage }" fixed="top" toggleable="lg">
      <img v-if="screenWidth >= 992 && view.topOfPage" src="../assets/img/logo-white.png" alt="peak-link-logo"
        class="logo">
      <img v-if="!view.topOfPage || screenWidth < 992" src="../assets/img/logo-color.png" alt="peak-link-logo"
        class="logo">
      <b-navbar-toggle target="nav-collapse">
        <template #default="{ expanded }">
          <b-icon v-if="expanded" icon="x-lg"></b-icon>
          <b-icon v-else icon="list"></b-icon>
        </template>
      </b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="ml-auto" style="gap: 15px">
          <b-nav-item v-for="(item, index) in title" :key="index" v-scroll-to="'#' + item.scroll"
            @click="goToRoute(item.scroll)"> {{ item.text }}</b-nav-item>
          <div v-if="isAr || !isAr" @mouseover="mouseOver" @mouseleave="mouseleave" @click="change_language">
            <b-button>{{ display_lang }}</b-button>
          </div>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>

export default {
  name: "Navbar",
  components: {
    // Scroll,
  },
  data() {
    return {
      en: "English",
      ar: "العربية",
      lang: "",
      scroll: "",
      title: [
        {
          text: "Home",
          scroll: "home"
        },
        {
          text: "About",
          scroll: "about"
        },
        {
          text: "Services",
          scroll: "service"
        },
        {
          text: "Product",
          scroll: "product"
        },
        {
          text: "Portfolio",
          scroll: "portfolio"
        },
        {
          text: "Partners",
          scroll: "partner"
        },
        {
          text: "Join Us",
          scroll: "join-us"
        },
      ],
      view: {
        topOfPage: true
      },
      screenWidth: 0,
      display_lang: '',
    };
  },
  computed: {
    isAr() {
      this.lang = localStorage.getItem("lang");
      if (this.lang == "en")
        this.display_lang = "English"
      else
        this.display_lang = "العربية"
      return this.lang == "ar";
    },
  },
  methods: {
    mouseOver() {
      if (this.display_lang == "English")
        this.display_lang = "العربية"
      else
        this.display_lang = "English"
    },
    mouseleave() {
      if (this.display_lang == "English")
        this.display_lang = "العربية"
      else
        this.display_lang = "English"
    },
    handleChange(selectedLang) {
      localStorage.setItem("lang", selectedLang);
      window.location.reload();
    },
    change_language(){
      localStorage.setItem("lang", 'en');

    },
    handleScroll() {
      if (window.pageYOffset > 100) {
        if (this.view.topOfPage) {
          this.view.topOfPage = false
          // console.log(false)
        }

      } else {
        if (!this.view.topOfPage) {
          this.view.topOfPage = true
          // console.log(true)
        }
      }
    },
    onScreenResize() {
      window.addEventListener("resize", () => {
        this.updateScreenWidth();
      });
    },
    updateScreenWidth() {
      this.screenWidth = window.innerWidth;
    },
    goToRoute(goal) {
      if (this.$route.name == "get-product" || this.$route.name == "get-job" || this.$route.name == "order-project") {
        this.$router.replace({ name: 'landing' })
        this.scroll = goal
        // console.log(this.scroll)
        localStorage.setItem("scroll", this.scroll)
      }
      if (goal == "join-us")
      this.$router.replace({ name: 'get-job' })
    }
  },
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll)
  },
  mounted() {
    this.updateScreenWidth();
    this.onScreenResize();
  },

};
</script>

<style lang="scss">
.my-navbar .navbar-light .navbar-nav a.nav-link {
  color: #fff;
  font-size: 15px;
  // font-weight: 500;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease-in;
  letter-spacing: 2px;

  &.onScroll {
    color: var(--main-blue);
  }
}

.my-navbar .navbar-light .navbar-nav .nav-link:focus {
  color: #fff !important;
}

.my-navbar .navbar-light.onScroll .navbar-nav a.nav-link {
  color: var(--main-blue) !important;
  // font-weight: 800;
  transition: all 0.3s ease-in;
}

.my-navbar .navbar-light .navbar-nav a.nav-link:hover {
  color: #fff;
  border-bottom-color: #fff !important;
  transition: all 0.3s ease-in;
}

.my-navbar .navbar-light.onScroll .navbar-nav a.nav-link:hover {
  border-bottom-color: var(--main-blue) !important;
  transition: all 0.3s ease-in;
}

.my-navbar .navbar-light {
  margin: 0px !important;
  transition: all .5s ease-in;

  &.onScroll {
    box-shadow: 0 0 10px #aaa;
    background-color: #fffffff7;
  }
}

@media (max-width: 991px) {
  .my-navbar .navbar {
    height: auto !important;
  }

  .my-navbar .navbar-light {
    background-color: #fffffff7;
    box-shadow: 0 0 10px #aaa;
  }

  .my-navbar .navbar-light .navbar-nav a.nav-link {
    color: var(--main-blue) !important;
    font-weight: 800 !important;
  }

  .my-navbar .navbar-light .navbar-nav a.nav-link:hover {
    border-bottom-color: var(--main-blue) !important;
    transition: all 0.3s ease-in;
  }

  .my-navbar .btn {
    color: var(--main-blue) !important;
    transition: all 0.3s ease-in;
  }

}

.my-navbar .btn {
  width: 80px !important;
  background-color: transparent !important;
  border: none !important;
  border-bottom: 1px solid transparent !important;
  border-radius: 0px !important;
  // font-weight: bold !important;
  transition: all 0.3s ease-in;
}

.my-navbar .onScroll .btn {
  color: var(--main-blue) !important;
  transition: all 0.3s ease-in;
}

.my-navbar .btn-secondary:focus,
.my-navbar .btn-secondary.focus {
  box-shadow: none !important;
}

.my-navbar .navbar-nav {
  margin-top: 15px;
  margin-bottom: 10px !important;
  margin-left: 20px;
  align-items: center;
}

.my-navbar .navbar-expand-lg {
  margin-right: 0.5vw;
}

.my-navbar .dropdown-toggle::after {
  display: none !important;
}

.my-navbar .dropdown .dropdown-menu {
  transition: all 0.3s ease-in;
  max-height: 0;
  display: block;
  opacity: 0;
}

.my-navbar .dropdown.show .dropdown-menu {
  max-height: 200px;
  opacity: 1;
}

.my-navbar .logo {
  margin-left: 2vw;
  width: 50px;
}

@media (max-width: 991px) {
  .my-navbar .hidden {
    display: none !important;
  }

  .my-navbar .dropdown-toggle {
    margin-left: 45px !important;
  }
}

.my-navbar .navbar .collapsed,
.my-navbar .navbar .not-collapsed {
  border: none;
  color: var(--main-blue) !important;
  font-size: 25px;
}

.my-navbar .navbar .not-collapsed {
  font-size: 25px;
  margin-top: 10px;
  margin-right: 5px;
}


.my-navbar .navbar .bi-list {
  font-size: 40px;
}
</style>
